import React, { Component } from 'react'
import {
    Navbar,
    Nav,
    FormControl,
    Container,
    Form,
    Button
} from 'react-bootstrap';
import logo from '../images/logo.png';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';

import Home from '../pages/Home';
import People from '../pages/People';

export default class Header extends Component {
    render() {
        return (
            <>
                <Navbar collapseOnSelect expand='lg' bg='dark' variant='dark'>
                    <Container>
                        <Navbar.Brand href='/' className='d-flex align-items-center'>
                            <img className='logo__img' src={logo} alt='logo' />
                            <h2>Архитекторы Беларуси</h2>
                        </Navbar.Brand>
                        <Navbar.Toggle aria-controls='responsive-navbar-nav' />
                        <Navbar.Collapse className='navbar' id='responsive-navbar-nav'>
                            <Nav>
                                <Nav.Link className='nav__button' href='/Architects/home'>Главная</Nav.Link>
                                <Nav.Link className='nav__button' href='/Architects/people'>Деятели</Nav.Link>
                            </Nav>
                            <div className='d-flex btn-group'>
                                <Button className='active' variant='primary'>Рус</Button>
                                <Button className='' variant='primary'>Англ</Button>
                            </div>
               
                            {/* <Form className='d-flex'>
                                <FormControl
                                    type='text'
                                    placeholder='Search'
                                    className='mr-sm-2'
                                />
                                <Button variant='outline-info'>Поиск</Button>
                            </Form>*/}
                        </Navbar.Collapse>
                    </Container>
                </Navbar>
                <Router>
                    <Switch>
                        <Route exact path='/Architects/' component={Home} />
                        <Route exact path='/Architects/people' component={People} />
                    </Switch>
                </Router>
            </>
        )
    }
}