import React, { Component } from 'react'

import {
    Container,
    Button,
    CardDeck,
    Card,
    Row,
    Col
} from 'react-bootstrap';

import logo from '../images/logo.png';

export default class People extends Component {
    render() {
        return (
       
            <Container fluid className='bg-dark text-light'>
                <Container className='d-flex justify-content-center py-4'>
                    <Row className='w-100'>
                        <Col xs={8}>
                            <a href='/' className='footer__logo d-flex align-items-center'>
                                <img
                                    className='logo__img'
                                    src={logo}
                                    alt='logo'
                                />
                                <h2>Архитекторы Беларуси</h2>
                            </a>
                        </Col>
                        <Col>
                            <div className='social__icons d-flex mt-3'>
                                <h5>Мы в соцсетях:</h5>
                                <a><i class="social__icon fab fa-telegram-plane"></i></a>
                                <a><i class="social__icon fab fa-instagram"></i></a>
                                <a><i class="social__icon fab fa-youtube"></i></a>
                            </div>
                            

                        </Col>
                    </Row>
                </Container>
            </Container >
        )
    }
}