import React, { Component } from 'react';
import Carousel from 'react-bootstrap/Carousel';

import castleMir from '../images/slider/castle-mir.jpg';
import castleNecv from '../images/slider/castle-nesv.jpg';
import church from '../images/slider/church.jpg';


export default class Slider extends Component {
    render() {
        return (
            <Carousel>
                <Carousel.Item>
                    <div className='bg-image'>
                        <img
                            className='slider__img d-block w-100'
                            src={castleMir}
                            alt='Мирский замок'
                        />
                        <div className='mask slider__mask'>
                            <Carousel.Caption>
                                <h3>Мирский замок</h3>
                                <p>
                                    Замково-парковый комплекс «Мир» — оборонительное укрепление и резиденция в городском посёлке Мир
                                    Кореличского района Гродненской области Беларуси. Памятник архитектуры, внесён в список Всемирного наследия
                                    ЮНЕСКО. Архитектурный комплекс включает в себя замок XVI—XX веков, валы XVII—XVIII веков, пруд 1896—1898 годов,
                                    часовню-усыпальницу Святополк-Мирских с домом сторожа и воротами, пейзажный и регулярный парки, дом управляющего.
                                </p>
                            </Carousel.Caption>
                        </div>
                    </div>
                </Carousel.Item>
                <Carousel.Item>
                    <div className='bg-image'>
                        <img
                            className='slider__img d-block w-100'
                            src={castleNecv}
                            alt='Несвижский замок'
                        />
                        <div className='mask slider__mask'>
                            <Carousel.Caption>
                                <h3>Несвижский замок</h3>
                                <p>
                                    Дворцово-замковый комплекс, находящийся в северо-восточной части города Несвижа в Минской области
                                    Беларуси, памятник архитектуры XVI—XVIII веков. Заложен князем Н. Х. Радзивиллом Сироткой в 1583 году,
                                    на месте деревянного замка. Включает в себя собственно замок, замковые укрепления, а также большой ландшафтно-пейзажный парк.
                                </p>
                            </Carousel.Caption>
                        </div>
                    </div>
                </Carousel.Item>
                <Carousel.Item>
                    <div className='bg-image'>
                        <img
                            className='slider__img d-block w-100'
                            src={church}
                            alt='Церковь Святого Духа'
                        />
                        <div className='mask slider__mask'>
                            <Carousel.Caption>
                                    <h3>Церковь Святого Духа</h3>
                                    <p>
                                        Церковь Святого Духа — греко-католический храм (позднее православный) в Минске, построенный в XVII веке.
                                        Находилась на Высоком Рынке и являлась доминантой комплекса мужского и женского греко-католических
                                        монастырей базилиан. В здании церкви сочетались черты готики и северного ренессанса. Главный фасад был
                                        украшен нишами, расписанными фресками с изображениями святых.
                                    </p>
                            </Carousel.Caption>
                        </div>
                    </div>
                </Carousel.Item>
            </Carousel>

        )
    }
}