import React, { Component } from 'react'

import {
    Container,
    Button,
    Card,
    Row,
    Col
} from 'react-bootstrap';

import levin from '../images/architects/Levin.jpg';


export default class Slider extends Component {
    render() {
        return (
            <Container>
                <h2 className='text-center m-5'>Деятель дня</h2>
                <div className='text-center'>
                    <Row className="bg-light g-0 mb-5 position-relative">
                        <Col className="col-md-3 mb-md-0 p-md-4">
                            <img src={levin} class="w-100" alt="Леонид Менделевич Левин" />
                        </Col>

                        <Col className="col-md-8 p-5 ps-md-5">
                            <h5 className="mt-0">Леонид Менделевич Левин</h5>
                            <span>25 июля 1936 — 1 марта 2014</span>
                            <p> Заслуженный архитектор Республики Беларусь, Лауреат Ленинской премии и премии Ленинского комсомола, Лауреат премии Волгоградского Комсомола, дважды лауреат Государственной премии Республики Беларусь, лауреат многих престижных конкурсов и смотров архитектурных работ, Академик Международной и Белорусской Академий архитектуры, Председатель Союза белорусских еврейских общественных объединений и общин с 1991 года. Один из авторов мемориального комплекса «Хатынь», за который (в составе авторского коллектива) был удостоен Ленинской премии.</p>
                            <a href="#">Подробнее</a>
                        </Col>
                    </Row>
                </div>
            </Container>
        )
    }
}